 <template>
  <div>
    <div>项目详情</div>
    <router-link to="/xmsx/xmxq/xmcy">项目成员</router-link>
  </div>
</template>
<template>
  <div class="main">
    <div class="nav-list">
      <commonLists :configs="configs"></commonLists>
      <loadmore></loadmore>
    </div>
  </div>
</template>
<script>
import commonLists from '../widget/commonLists';
import loadmore from '../common/loadmore';
import store from '../../vuex';
export default {
  name: 'app-xmcy',
  data () {
    return {
      configs: {
        type: 'memberList',
        list: store.state.membersModule.memberList
      }
    }
  },
  components: {
    commonLists,
    loadmore
  },
  methods: {

  },
  mounted(){
    let _this = this;
    // 第一次加载成员列表
    this.$http.get('/api/members').then(function (data){
      if(data.data.success){
        let members = data.data.data;
        members.forEach( item => {
          _this.configs.list.push({
            text: item.text,
            name: item.name
          });
        });
      }else{
        console.log('error');
      }
    },function (err){
      console.log(err);
    });
  }
}
</script>
<style scoped>
.header{
  position: fixed;
  z-index: 1;
}
body{overflow: auto;}
#app{
  display: -webkit-box;
  -webkit-box-orient: vertical;
}
</style>


