import Vue from 'vue';
import echarts from 'echarts';

import getTabData from '@/utils/getTabData.js';
import getYscale from '@/utils/getYscale.js';
import getXscale from '@/utils/getXscale.js';
import handleSeries from '@/utils/handleSeries.js';
import getLegend from '@/utils/getLegend.js';

const state = {
  //echarts图数据
  rtOption: {
	    grid: {
	      left: 60,
	      bottom: 140
	    },
	    tooltip: {
	      trigger: 'axis'
	    },
	    legend: {
	    	show:true,	
	      data: [],
	      bottom: 0,
	      itemWidth: 18,
	      itemHeight: 18,
	      icon: 'stack',
	      left: 30,
	      align: 'left',
	      textStyle: {
	        fontSize: 20,
	        fontFamily: 'PingFangSC-Regular'
	      }
	    },
	    xAxis: {
	      type: 'category',
	      boundaryGap: false,
	      data: []
	    },
	    yAxis: {
	      type: 'value',
	      min: 0,
	      max: 10,
	      interval: 2,
	      axisLabel: {
	        formatter: '{value}'
	      },
	      splitLine: {
	        show: false
	      },
	      axisTick: {
	        show: false
	      }
	    },
	    series: []
  },
  rsOption: {
	    grid: {
	      left: 60,
	      bottom: 140
	    },
	    tooltip: {
	      trigger: 'axis'
	    },
	    legend: {
	      data: [],
	      bottom: 0,
	      itemWidth: 18,
	      itemHeight: 18,
	      icon: 'stack',
	      left: 30,
	      itemGap: 20,
	      align: 'left',
	      textStyle: {
	        fontSize: 20,
	        fontFamily: 'PingFangSC-Regular'
	      }
	    },
	    xAxis: {
	      type: 'category',
	      boundaryGap: false,
	      data: []
	    },
	    yAxis: {
	      type: 'value',
	      min: 0,
	      max: 10,
	      interval: 2,
	      axisLabel: {
	        formatter: '{value}'
	      },
	      splitLine: {
	        show: false
	      },
	      axisTick: {
	        show: false
	      }
	    },
	    series: []
  },
  rwOption: {
	    grid: {
	      left: 60,
	      bottom: 140
	    },
	    tooltip: {
	      trigger: 'axis'
	    },
	    legend: {
	      data: [],
	      bottom: 0,
	      itemWidth: 18,
	      itemHeight: 18,
	      icon: 'stack',
	      left: 30,
	      itemGap: 20,
	      align: 'left',
	      textStyle: {
	        fontSize: 20,
	        fontFamily: 'PingFangSC-Regular'
	      }
	    },
	    xAxis: {
	      type: 'category',
	      boundaryGap: false,
	      data: []
	    },
	    yAxis: {
	      type: 'value',
	      min: 0,
	      max: 10,
	      interval: 2,
	      axisLabel: {
	        formatter: '{value}'
	      },
	      splitLine: {
	        show: false
	      },
	      axisTick: {
	        show: false
	      }
	    },
	    series: []
  },
  //表格数据
  rtTableConfigs:{//人天数据
	  showDMW:true,//是否展示图表的月/周/日
	  topTitles:['部门','需求','设计','开发','测试','上线','总人天数'],//表格上面头部
	  daLists:[]  
	},
  rsTableConfigs:{//人数数据
    showDMW:true,//是否展示图表的月/周/日
    topTitles:['部门','需求','设计','开发','测试','上线','总人数'],//表格上面头部
    daLists:[]  
  },
	rwTableConfigs:{//任务数数据
    showDMW:true,//是否展示图表的月/周/日
    topTitles:['部门','需求','设计','开发','测试','上线','总任务数'],//表格上面头部
    daLists:[] 
  }
};

const mutations = {
  //获取echarts数据
  getGzlEchartsData(state, option) {
  	var param = option.param;
		var date = option.date;
		if(param == 1){
			var api = '';
	    //判断日、周、月，请求不同接口
	    switch (date) {
	      case 'day':
	        api = 'processDay';
	        break;
	      case 'week':
	        api = 'processWeek';
	        break;
	      case 'month':
	        api = 'processMonth';
	    };

	    Vue.$http.get(api).then(function(data) {
	      var da = data.data.data;
	      var scaleDa = da.scale;
	      var listsDa = da.lists;
	      //重置echarts的option
	    	state.rtOption.xAxis.data = getXscale(scaleDa);
	      state.rtOption.yAxis.max = getYscale(listsDa).max;
	      state.rtOption.yAxis.interval = getYscale(listsDa).interval;
	      state.rtOption.series = handleSeries(listsDa);
	      state.rtOption.legend.data = getLegend(listsDa);
	      var gzlEharts = echarts.init(document.querySelector('#echarts'));
    	  gzlEharts.setOption(state.rtOption)
	    })
		}else if(param == 2){
			var api = '';
	    //判断日、周、月，请求不同接口
	    switch (date) {
	      case 'day':
	        api = 'processDay';
	        break;
	      case 'week':
	        api = 'processWeek';
	        break;
	      case 'month':
	        api = 'processMonth';
	    };

	    Vue.$http.get(api).then(function(data) {
	      var da = data.data.data;
	      var scaleDa = da.scale;
	      var listsDa = da.lists;
	      //重置echarts的option
	      state.rsOption.xAxis.data = getXscale(scaleDa);
	      state.rsOption.yAxis.max = getYscale(listsDa).max;
	      state.rsOption.yAxis.interval = getYscale(listsDa).interval;
	      state.rsOption.series = handleSeries(listsDa);
	      state.rsOption.legend.data = getLegend(listsDa);
	      var gzlEharts = echarts.init(document.querySelector('#echarts'));
    	  gzlEharts.setOption(state.rsOption)
	    })
		}else{
			var api = '';
	    //判断日、周、月，请求不同接口
	    switch (date) {
	      case 'day':
	        api = 'processDay';
	        break;
	      case 'week':
	        api = 'processWeek';
	        break;
	      case 'month':
	        api = 'processMonth';
	    };

	    Vue.$http.get(api).then(function(data) {
	      var da = data.data.data;
	      var scaleDa = da.scale;
	      var listsDa = da.lists;
	      //重置echarts的option
  	    state.rwOption.xAxis.data = getXscale(scaleDa);
	      state.rwOption.yAxis.max = getYscale(listsDa).max;
	      state.rwOption.yAxis.interval = getYscale(listsDa).interval;
	      state.rwOption.series = handleSeries(listsDa);
	      state.rwOption.legend.data = getLegend(listsDa);
	      var gzlEharts = echarts.init(document.querySelector('#echarts'));
    	  gzlEharts.setOption(state.rwOption)
	    })
		}
  },
  //获取表格数据
  getGzlTableData(state, data) {
  	if(data == 1){
  		Vue.$http.get('taskmandaytab').then(function(data) {
	       var da = data.data.data;
		   var totleData = {
		   	title:"人天数合计",
		   	data:[0,0,0,0,0,0]
		   }
	      var tableData = getTabData(da,totleData);
	      state.rtTableConfigs.daLists = tableData;
	    })
  	}else if(data == 2){
  		Vue.$http.get('taskmannumtab').then(function(data) {
	       var da = data.data.data;
		   var totleData = {
		   	title:"人数合计",
		   	data:[0,0,0,0,0,0]
		   }
	      var tableData = getTabData(da,totleData);
	      state.rsTableConfigs.daLists = tableData;
	    })
  	}else{
  		Vue.$http.get('tasknumbertab').then(function(data) {
	       var da = data.data.data;
		   var totleData = {
		   	title:"任务数合计",
		   	data:[0,0,0,0,0,0]
		   }
	      var tableData = getTabData(da,totleData);
	      state.rwTableConfigs.daLists = tableData;
	    })
  	}
  }
};

const actions = {
  getGzlEchartsData(store, date) {
    store.commit('getGzlEchartsData', date);
  },
  getGzlTableData(store,data) {
    store.commit('getGzlTableData',data);
  }
};

const indexModule = {
  state,
  mutations,
  actions
};
export default indexModule;



