<template>
 <commonCharts :configs="configs"></commonCharts>
</template>

<script>
import echarts from 'echarts';
import commonCharts from '../widget/commonCharts';
import {mapState} from 'vuex';
export default {
  name:'app-xmzt',
  data(){
  	return{
  	}
  },
  computed:{
    ...mapState({
      configs:state=>state.xmztModule.xmztTableConfigs
    })
  },
  created:function(){
    this.$store.dispatch('getXmztTableData');
    this.$store.dispatch('getXmztEchartsData','month');
  },
  components:{commonCharts},
   mounted:function(){
    var ztEharts = echarts.init(document.querySelector('#echarts'));
    var option = {
      grid:{
          left:60,
          bottom:170      
      },
      tooltip: {
          trigger: 'axis'
      },
      legend: {
        data:['未启动   ','正常进行 ','已延期','部分上线','暂停/取消','已上线'],
        bottom:0,
        itemWidth:18,
        itemHeight:18,
        icon:'stack',
        left:40,
        itemGap:20,
        align:'left',
        textStyle:{
          fontSize:20,
          fontFamily:'PingFangSC-Regular'
        }
      },
      xAxis:  {
          type: 'category',
          boundaryGap: false,
          data: ['2017\n07.12','2017\n07.12','2017\n07.12','2017\n07.12','2017\n07.12','2017\n07.12']
      },
      yAxis: {
          type: 'value',
          min: 0,
          max: 10,
          interval: 2,
          axisLabel: {
              formatter: '{value}'
          },
          splitLine:{
              show:false
          },
          axisTick:{
            show:false
          }
      },
      series: [
          {
              name:'未启动   ',
              type:'line',
              data:['', '', 5, 3, 2, 10]
          },
          {
              name:'正常进行 ',
              type:'line',
              data:[1, 6, 1, 5, 3, 9]
          },
          {
              name:'已延期',
              type:'line',
              data:[1, 1, 2, 5, 3, 8]
          },
          {
              name:'部分上线',
              type:'line',
              data:[1, 2, 2, 5, 3, 7]
          },
          {
              name:'暂停/取消',
              type:'line',
              data:[1, 3, 2, 5, 3, 6]
          },
          {
              name:'已上线',
              type:'line',
              data:[1, 3, 2, 5, 3, 5]
          }
      ]
    };
    ztEharts.setOption(option)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='sass'>
/*@import '../../assets/sass/xmjd.scss';*/
</style>




