<template>
  <div class="main">
    <div class="nav-list">
      <commonLists :configs="configs"></commonLists>
    </div>
    <router-view></router-view>
  </div>
</template>
<script>
import commonLists from '../widget/commonLists';
import { mapState } from 'vuex';
import store from '../../vuex';
export default {
  name: 'app-sxjg',
  data () {
    return {
      configs: {
        type: 'listNav',
        list: []
      }
    }
  },
  components: {
    commonLists
  },
  computed:{
    ...mapState({
      filter:state=>state.xmsxModule.filter
    })
  },
  mounted(){
    let _this = this,
        {centerId, departId, projectId, stateId, riskId, typeId} = this.filter;

    this.$http.get('/api/filterResult',{
      params: {
        centerId,
        departmentId: departId,
        projectTypeId: projectId,
        processId: stateId,
        riskTypeId: riskId,
        projectStatusId: typeId
      }
    }).then(function (data){
      if(data.data.success){
        let filterResult = data.data.data;
        filterResult.forEach( (v, i) => {
          _this.configs.list.push({
            text: v.projectName,
            to: '/xmsx/xmxq?projectId='+ v.projectId
          });
        });
      }else{
        console.log('error');
      }
    },function (err){
      console.log(err);
    });
  }
}
</script>



