import Vue from 'vue';

const state = {
  memberList: [],
  loadCount: 1,
  isLoading: true,
  isWillLoad: false,
  descritText: '上拉加载更多'
};

const mutations = {
  setIsLoad(state, flag){
    state.isWillLoad = flag;
  },
  loadCount(state){
    state.loadCount ++;
  },
  loadMore(state){
    state.isLoading = false;
    state.descritText = '加载中。。。请稍后';
    console.log('loading');
    setTimeout(function (){
      [1, 2].forEach( item => {
        state.memberList.push({
          text: 'text',
          name: state.memberList.length + 1 + '...name'
        });
      });
      state.isLoading = true;
      state.descritText = '上拉加载更多';
      console.log('loading end');
    }, 5000);
  }
};

const actions = {
  setIsLoad(context, flag){
    context.commit('setIsLoad', flag);
  },
  loadMore(context){
    context.commit('loadMore');
  },
  loadCount(){
    context.commit('loadCount');
  }
};

const indexModule = {
  state,
  mutations,
  actions
};
export default indexModule;


